import styled from "styled-components";

const Button = styled.button`
  background-color: #fff;
  color: #000;
  padding: 5px 20px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 20px;
  margin-top: 10px;
  border: none;
  cursor: pointer;
  flex: 1;
`;

export default Button;
